





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import JobEditor from "@/components/misc/JobEditor.vue";
import {
  Overwrite,
  IServerResponse,
  ICreateBackupJobResponseSuccess
} from "@/shared";
import {
  repositoriesModule,
  jobsModule,
  routerModule,
  adminModule
} from "@/store/modules";
import { IJob } from "@/store/modules/JobsModule";
import { IBackupJobWithClientId } from "./Client.vue";

@Component({
  components: {
    JobEditor
  }
})
export default class NewJobView extends Mixins() {
  get clientId(): string {
    return routerModule.params.clientId;
  }

  async onSubmit(
    job: Overwrite<
      IJob,
      {
        id?: number;
      }
    >
  ) {
    // TODO: infer args from other class
    // TODO: trycatch
    if (!this.clientId) {
      const resp = await jobsModule.createJob(job);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/jobs/${resp.payload.job.id}`
        });
      }
    } else {
      console.log("client!!!");
      /*const cjob: IBackupJobWithClientId = {
        clientId: this.clientId,
        clientjob: job
      };
      const resp = await adminModule.createBackupJobOnClient(cjob);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/admin/client/${this.clientId}`
        });
      }*/
    }
  }
}
